import axios from 'axios'

// ES6 Promise
export function request(options) {
  return new Promise((resolve, reject) => {
    // 1.axios
    const instance = axios.create({
      baseURL: 'http://123.207.32.32:8000',
      timeout: 5000
    })

    // ()
    instance.interceptors.response.use(res => {
      return res.data
    })

    // 
    instance(options)
        .then(res => {
          resolve(res)
        }).catch(err => {
          reject(err)
    })
  })
}

